import {Entity, Column, PrimaryGeneratedColumn, ManyToOne, JoinColumn} from "typeorm";
import { User } from "./user";

@Entity()
export class Link{
    @PrimaryGeneratedColumn()
    public id!: number;
    @ManyToOne(type => User, user => user.id)
    @JoinColumn()
    public user!: User;
    @Column()
    public url!: String;
    @Column()
    public title!: string;
}